import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GlobalVariable } from '../../utils/global';

@Injectable()
export class TagService {

  constructor(private http:Http) { }

  getAll() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

  return this.http.get(GlobalVariable.BASE_API_URL+'tags/', {headers: headers})
    .map(res => res.json());
  }

  getById(_id) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

  return this.http.get(GlobalVariable.BASE_API_URL+'tags/'+_id, {headers: headers})
    .map(res => res.json());
  }

  add(data) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.post(GlobalVariable.BASE_API_URL+'tags/add', data, {headers: headers})
      .map(res => res.json());
  }

  edit(_id, data) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.post(GlobalVariable.BASE_API_URL+'tags/edit/'+_id, data, {headers: headers})
      .map(res => res.json());
  }

  delete(_id) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'tags/delete/'+_id, {headers: headers})
      .map(res => res.json());
  }

}



