import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GlobalVariable } from '../../utils/global';

@Injectable()
export class UserService {

  constructor(private http:Http) { }

  getAll() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'users/', {headers: headers})
    .map(res => res.json());
  }

  getById(_id) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'users/'+_id, {headers: headers})
    .map(res => res.json());
  }

  add(data) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.post(GlobalVariable.BASE_API_URL+'users/register/', data, {headers: headers})
      .map(res => res.json());
  }

  edit(_id, data) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.post(GlobalVariable.BASE_API_URL+'users/update/'+_id, data, {headers: headers})
      .map(res => res.json());
  }

  delete(_id) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'users/delete/'+_id, {headers: headers})
      .map(res => res.json());
  }

}



