import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { TagService } from '../../services/tag/tag.service';

@Component({
  selector: 'app-tag',
  templateUrl: './tag.component.html',
  styleUrls: ['./tag.component.css']
})
export class TagComponent implements OnInit {
  tags:   any;
  value:  String;
  _id:    String;

  @ViewChild('close') close: ElementRef;

  constructor(private tagService:TagService) { }

  ngOnInit() {
    this.load();
  }

  onSubmit() {
    let tag = {
      value: this.value
    };

    if (this._id === undefined) {
      // add
      this.tagService.add(tag).subscribe(data => {
        this.closeModal();
        this.load();
        return;
      })
    }

    this.tagService.edit(this._id, tag).subscribe(data => {
      this.closeModal();
      this.load();
      return;
    })
  }

  load() {
    this.tagService.getAll().subscribe(data => {
      this.tags = data;
    })
  }

  addModal() {
    this.reset();
  }

  editModal(_id) {
    this._id = _id;

    this.tagService.getById(_id).subscribe(data => {
      this.value = data.value;
    });
  }

  delete(_id) {
    this.tagService.delete(_id).subscribe(data => {
      this.load();
    })
  }
  closeModal() {
    this.close.nativeElement.click();
    this.load();
  }

  reset() {
    this._id = undefined;
    this.value = undefined;
  }
}



