import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { SliderService } from '../../services/slider/slider.service';
import { UploadService } from '../../services/upload/upload.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { GlobalVariable } from '../../utils/global';

@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.css']
})
export class SliderComponent implements OnInit {
  _id:      String;
  sliders:  any;
  file:     File;
  base:     String;

  @ViewChild('close') close: ElementRef;
  @ViewChild('elUpload') elUpload: ElementRef;

  constructor(private sliderService:SliderService,
              private uploadService:UploadService,
              private flashMessage:FlashMessagesService
            ) { }

  ngOnInit() {
    this.base = GlobalVariable.BASE_API_URL;
    this.sliderService.getAll().subscribe(data => {
      this.sliders = data;
    });
  }

  changeListener($event) : void {
    this.readThis($event.target);
  }

  readThis(inputValue: any): void {
    this.file = inputValue.files[0];
  }

  // delete
  delete(_id) {
    this.sliderService.delete(_id).subscribe(data => {
      this.sliderService.getAll().subscribe(data => {
        this.sliders = data;
      });
    });
  }

  onSubmit() {
    if (this.file) {
      this.uploadService.uploadFile(this.file, null).subscribe(data => {
        if (data.success) {

          // add
          if (this._id === undefined){
            this.sliderService.add({image: data.filename}).subscribe(data => {
              this.closeModal();
              return;
            });
          }

          // edit
          this.sliderService.edit({_id: this._id, image: data.filename}).subscribe(data => {
            this.closeModal();
            return;
          });
        }
      },
      err => {
        console.log(err);
      });
    }

  }

  closeModal() {
    this.close.nativeElement.click();
    this.sliderService.getAll().subscribe(data => {
      this.sliders = data;
    });
  }

  addModal() {
    this._id = undefined;
  }
  editModal(_id) {
    this._id = _id;
  }
}



