import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { BlogService } from '../../services/blog/blog.service';
import { UploadService } from '../../services/upload/upload.service';
import { GlobalVariable } from '../../utils/global';

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css']
})
export class BlogComponent implements OnInit {
  blogs:    any;
  _id:      String;
  title:    String;
  des:      String;
  body:     String;
  author:   String;
  image:    String;
  date:     Date;
  hidden:   Boolean;
  file:     File;
  base:     String;
  comments: any;

  @ViewChild('close') close: ElementRef;
  @ViewChild('close2') close2: ElementRef;
  @ViewChild('elUpload') elUpload: ElementRef;

  constructor(private blogService:BlogService,
              private elRef:ElementRef,
              private uploadService:UploadService
  ) { }

  ngOnInit() {
    this.base = GlobalVariable.BASE_API_URL;
    this.load();
  }

  ngAfterViewInit() {
  }

  onSubmit() {

    let blog:any = new Object();
    blog.title = this.title;
    blog.des = this.des;
    blog.body = this.elRef.nativeElement.querySelector('.note-editable').innerHTML || '';
    blog.author = this.author;
    blog.hidden = this.hidden;

    // upload file
    if (this.file) {
      let options = {
        file: this.file,
        name: 'image',
        des: 'assets/images/blog/',
      };

      this.uploadService.upload(options).subscribe(data => {
        // upload success
        if(data.success){
          blog.image = data.filename;
          
           // add
          if (this._id === undefined) {
            this.blogService.add(blog).subscribe(data => {
              this.closeModal();
              this.load();
            });
          } 

          // edit
          else {
            this.blogService.edit(this._id, blog).subscribe(data => {
              this.closeModal();
              this.load();
            });
          }

        }
      });
    } else {

      // add
      if (this._id === undefined) {
        this.blogService.add(blog).subscribe(data => {
          this.closeModal();
          this.load();
        });
      } 

      // edit
      else {
        this.blogService.edit(this._id, blog).subscribe(data => {
          this.closeModal();
          this.load();
        });
      }
    }
  }

  load() {
    this.blogService.getAll().subscribe(data => {
      this.blogs = data.data;
    })
  }

  addModal() {
    
    this.reset();
  }

  editModal(_id) {
    this._id = _id;

    this.blogService.getById(_id).subscribe(data => {
      this.title = data.title;
      this.des = data.des;
      if(this.elRef.nativeElement.querySelector('.note-editable')){
        this.elRef.nativeElement.querySelector('.note-editable').innerHTML = data.body;
      }
      this.author = data.author
      this.date = data.date;
      this.image = data.image;
      this.hidden = data.hidden;
    })
  }

  delete(_id) {
    this.blogService.delete(_id).subscribe(data => {
      this.load();
    })
  }
  closeModal() {
    this.close.nativeElement.click();
    this.load();
  }

  reset() {
    this._id = undefined;
    this.title = undefined;
    this.des = undefined;
    this.body = undefined;
    this.author = undefined;
    this.elRef.nativeElement.querySelector('.note-editable').innerHTML = '';
    this.hidden = undefined;
  }

  changeListener($event) : void {
    this.readThis($event.target);
  }

  readThis(inputValue: any): void {
    this.file = inputValue.files[0];
  }

  // view comment by blog id
  viewComment(_id) {
    this._id = _id;
    this.blogService.getCommentsById(_id).subscribe(data => {
      this.comments = data.comments;
    },
    err => {
      console.log(err);
    })
  }

  // delete comment by id
  deleteComment(cmtId) {
    this.blogService.deleteComment(this._id, cmtId).subscribe(data => {
      this.close2.nativeElement.click();
      this.load();
    },
    err => {
      console.log(err);
    })
  }
}



