import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GlobalVariable } from '../../utils/global';

@Injectable()
export class BlogService {

  constructor(private http:Http) { }

  getAll() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'blogs/all', {headers: headers})
    .map(res => res.json());
  }

  getById(_id) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'blogs/'+_id, {headers: headers})
    .map(res => res.json());
  }

  add(data) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.post(GlobalVariable.BASE_API_URL+'blogs/add/', data, {headers: headers})
      .map(res => res.json());
  }

  edit(_id, data) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.post(GlobalVariable.BASE_API_URL+'blogs/edit/'+_id, data, {headers: headers})
      .map(res => res.json());
  }

  delete(_id) {
    let headers = new Headers();
      headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'blogs/delete/'+_id, {headers: headers})
      .map(res => res.json());
  }

  getCommentsById(_id) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'blogs/'+_id+'/comments', {headers: headers})
                    .map(res => res.json());
  }

  deleteComment(postId, cmtId) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.get(GlobalVariable.BASE_API_URL+'blogs/'+postId+'/comments/delete/'+cmtId, {headers: headers})
                    .map(res => res.json());
  }

}



