import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { GlobalVariable } from '../../utils/global';

@Injectable()
export class SliderService {

  	constructor(private http:Http) { }

  	getAll() {
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

		return this.http.get(GlobalVariable.BASE_API_URL+'sliders/', {headers: headers})
		  .map(res => res.json());
	}

	add(slider) {
		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

		return this.http.post(GlobalVariable.BASE_API_URL+'sliders/add', slider, {headers: headers})
		  .map(res => res.json());
	}
	
	edit(slider) {
		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

		return this.http.post(GlobalVariable.BASE_API_URL+'sliders/edit/'+slider._id, slider, {headers: headers})
		  .map(res => res.json());
	}
	
	delete(_id) {
		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

		return this.http.get(GlobalVariable.BASE_API_URL+'sliders/delete/'+_id, {headers: headers})
		  .map(res => res.json());
	}
}



