import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ContactService } from '../../services/contact/contact.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  _id:      String;
  name:     String;
  email:    String;
  content:  String;
  contacts: any;
  @ViewChild('close') close: ElementRef;

  constructor(private contactService:ContactService,
  ) { }

  ngOnInit() {
    this.load();
  }

  onSubmit() {
    let data = {
      name: this.name,
      email: this.email,
      content: this.email
    };
    if (this._id === undefined) {
      // add
      this.contactService.add(data).subscribe(data => {
        this.closeModal();
        this.load();
        return;
      })
    }

    this.contactService.edit(this._id, data).subscribe(data => {
      this.closeModal();
      this.load();
      return;
    })
  }

  load() {
    this.contactService.getAll().subscribe(data => {
      this.contacts = data;
    })
  }

  addModal() {
    this.reset();
  }

  editModal(_id) {
    this._id = _id;

    this.contactService.getById(_id).subscribe(data => {
      this.name = data.name;
      this.email = data.email;
      this.content = data.content;
    });
  }

  delete(_id) {
    this.contactService.delete(_id).subscribe(data => {
      this.load();
    })
  }
  closeModal() {
    this.close.nativeElement.click();
    this.load();
  }

  reset() {
    this._id = undefined;
    this.name = undefined;
    this.email = undefined;
    this.content = undefined;
  }

}



